import React from 'react'
import Helmet from 'react-helmet'
import Plans from '../partials/Plans'
import FooterInfo from '../partials/FooterInfo'

const PricingPage = () => (
  <div>
    <Helmet
      title="Fitato Pass Pricing | Choose Your Pass Plan"
    >
      <meta name="description" content="Fitato Pass offers a variety to fitness facilities. Choose the pass type that suits you. Giving you choice, control & convenience for your health goals." />
    </Helmet>
    <Plans tag="h1" />
    <FooterInfo />
  </div>
)

export default PricingPage



